@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;600;700&family=Fraunces:opsz,wght@9..144,500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;600;700&family=Fraunces:opsz,wght@9..144,500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.weekly-2026 {
  --bg: #f7f1e7;
  --panel: #fffaf2;
  --ink: #1f2a1f;
  --muted: #5f6b5f;
  --accent: #2f6b4f;
  --accent-light: rgba(47, 107, 79, 0.14);
  --border: rgba(31, 42, 31, 0.08);
  font-family: "DM Sans", "Trebuchet MS", sans-serif;
  color: var(--ink);
  background: radial-gradient(circle at top, #fffaf2 0%, #f4ead9 55%, #efe3cf 100%);
  padding: 32px 32px 40px;
  border-radius: 24px;
  box-shadow: 0 18px 40px rgba(31, 42, 31, 0.12);
  max-width: 1100px;
  margin: 0 auto;
}

.weekly-2026__header {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 20px;
  gap: 20px;
  align-items: stretch;
  justify-content: space-between;
  margin-bottom: 28px;
}

.weekly-2026__title-block {
  flex: 1 1 380px;
  background: var(--panel);
  border-radius: 18px;
  padding: 20px 24px;
  border: 1px solid var(--border);
  display: grid;
  grid-gap: 12px;
  gap: 12px;
}

.weekly-2026__eyebrow {
  font-size: 13px;
  letter-spacing: 0.14em;
  text-transform: uppercase;
  color: var(--muted);
}

.weekly-2026__title {
  font-family: "Fraunces", "Times New Roman", serif;
  font-size: 30px;
  line-height: 1.15;
  margin: 0;
}

.weekly-2026__meta {
  display: flex;
  grid-gap: 12px;
  gap: 12px;
  align-items: baseline;
  font-size: 14px;
}

.weekly-2026__meta-label {
  color: var(--muted);
  text-transform: uppercase;
  font-size: 11px;
  letter-spacing: 0.12em;
}

.weekly-2026__meta-value {
  font-weight: 600;
}

.weekly-2026__keyword {
  flex: 0 1 260px;
  background: var(--accent);
  color: #f8f6f1;
  border-radius: 18px;
  padding: 20px;
  display: grid;
  grid-gap: 10px;
  gap: 10px;
}

.weekly-2026__keyword-label {
  font-size: 13px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  opacity: 0.9;
}

.weekly-2026__keyword-value {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
}

.weekly-2026__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-gap: 20px;
  gap: 20px;
}

.weekly-2026__section {
  background: var(--panel);
  border-radius: 18px;
  padding: 18px 20px;
  border: 1px solid var(--border);
  display: grid;
  grid-gap: 14px;
  gap: 14px;
}

.weekly-2026__section--wide {
  margin-top: 20px;
}

.weekly-2026__section-header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  grid-gap: 12px;
  gap: 12px;
}

.weekly-2026__section-header h2 {
  margin: 0;
  font-size: 18px;
}

.weekly-2026__badge {
  background: var(--accent-light);
  color: var(--accent);
  padding: 4px 10px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 600;
}

.weekly-2026__list {
  display: grid;
  grid-gap: 12px;
  gap: 12px;
}

.weekly-2026__list-item {
  display: grid;
  grid-gap: 6px;
  gap: 6px;
  padding-bottom: 10px;
  border-bottom: 1px dashed rgba(31, 42, 31, 0.12);
}

.weekly-2026__list-item:last-child {
  border-bottom: none;
  padding-bottom: 0;
}

.weekly-2026__item-label {
  font-size: 13px;
  color: var(--muted);
}

.weekly-2026__item-value {
  font-size: 16px;
  font-weight: 500;
  white-space: pre-wrap;
}

.weekly-2026__item-value--mono {
  font-family: "SFMono-Regular", "Courier New", monospace;
  font-size: 14px;
}

@media (max-width: 720px) {
  .weekly-2026 {
    padding: 20px;
    border-radius: 18px;
  }

  .weekly-2026__header {
    flex-direction: column;
  }

  .weekly-2026__title {
    font-size: 24px;
  }
}

.weekly-2026-edit {
  --bg: #f7f1e7;
  --panel: #fffaf2;
  --ink: #1f2a1f;
  --muted: #5f6b5f;
  --accent: #2f6b4f;
  --border: rgba(31, 42, 31, 0.1);
  font-family: "DM Sans", "Trebuchet MS", sans-serif;
  color: var(--ink);
  background: radial-gradient(circle at top, #fffaf2 0%, #f4ead9 55%, #efe3cf 100%);
  padding: 28px 28px 32px;
  border-radius: 24px;
  box-shadow: 0 18px 40px rgba(31, 42, 31, 0.12);
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
  grid-gap: 22px;
  gap: 22px;
}

.weekly-2026-edit__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: 16px;
  gap: 16px;
  background: var(--panel);
  padding: 18px 22px;
  border-radius: 18px;
  border: 1px solid var(--border);
}

.weekly-2026-edit__eyebrow {
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.18em;
  color: var(--muted);
  margin: 0 0 6px;
}

.weekly-2026-edit__title {
  font-family: "Fraunces", "Times New Roman", serif;
  font-size: 26px;
  margin: 0;
}

.weekly-2026-edit__meta {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  grid-gap: 16px;
  gap: 16px;
  background: var(--panel);
  padding: 18px 20px;
  border-radius: 18px;
  border: 1px solid var(--border);
}

.weekly-2026-edit__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-gap: 18px;
  gap: 18px;
}

.weekly-2026-edit__section {
  background: var(--panel);
  padding: 18px 20px;
  border-radius: 18px;
  border: 1px solid var(--border);
  display: grid;
  grid-gap: 14px;
  gap: 14px;
}

.weekly-2026-edit__section h2 {
  margin: 0;
  font-size: 18px;
}

.weekly-2026-edit__section--wide {
  padding: 20px 22px;
}

.weekly-2026-edit__list {
  display: grid;
  grid-gap: 14px;
  gap: 14px;
}

.weekly-2026-edit__field {
  display: grid;
  grid-gap: 6px;
  gap: 6px;
}

.weekly-2026-edit__label {
  font-size: 13px;
  color: var(--muted);
}

.weekly-2026-edit textarea,
.weekly-2026-edit input {
  background: #fffdf8;
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 10px 12px;
  font-size: 14px;
}

.weekly-2026-edit textarea:focus,
.weekly-2026-edit input:focus {
  outline: 2px solid rgba(47, 107, 79, 0.2);
  border-color: rgba(47, 107, 79, 0.4);
  box-shadow: none;
}

@media (max-width: 720px) {
  .weekly-2026-edit {
    padding: 18px;
    border-radius: 18px;
  }

  .weekly-2026-edit__header {
    flex-direction: column;
    align-items: flex-start;
  }
}

.book-item {
  border: 1px solid #ccc;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
}

.summary {
  font-weight: bold;
}

.important {
  background-color: antiquewhite;
}
.original-text {
  display: none;
  font-style: italic;
  margin-top: 10px;
}

.show-original .original-text {
  display: block;
}

.read-button {
  background-color: #ff7f7f;
  border: none;
  color: white;
  padding: 5px 10px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 5px;
}

